import { useState } from "react";
import { Convert } from "csv-to-schem-converter";

import "./App.css";

let csvText = "";

async function convert() {
  const schemBuff = await Convert("1.18.1", csvText);

  const schemBlob = new Blob([schemBuff], { type: "application/octet-stream" });
  const link = document.createElement("a");

  link.href = URL.createObjectURL(schemBlob);
  link.download = "export.schem";
  link.click();
}
function App() {
  const [display, setDisplay] = useState(false);
  return (
    <div className="App" style={{ textAlign: "center" }}>
      <div>
        <table style={{ margin: "auto", paddingBottom: "24px" }}>
          <tr>
            <td>Px</td>
            <td>X</td>
          </tr>
          <tr>
            <td>Py</td>
            <td>Y</td>
          </tr>
          <tr>
            <td>Pz</td>
            <td>Z</td>
          </tr>
          <tr>
            <td>block</td>
            <td>(minecraft:stone )</td>
          </tr>
        </table>
      </div>
      <div>
        <input
          type="file"
          onChange={(e) =>
            e.target.files[0].text().then((text) => {
              csvText = text;
              setDisplay(true);
            })
          }
          multiple={false}
          accept="text/csv"
        />
        <button
          onClick={async () => {
            setDisplay(false);
            await convert();
            setDisplay(true);
          }}
          disabled={!display}
        >
          {" "}
          {" "}
        </button>
      </div>
    </div>
  );
}

export default App;
